@import 'reset-css';

html {
  font-size: 62.5%;
}

body * {
  font-size: 1.6rem;
  font-family: 'IBM Plex Sans', sans-serif;
  color: rgb(40, 40, 40);
}

main {
  position: relative;
  background: #efefef;
  // height: 100vh;
  padding-top: 200px;
  padding-bottom: 40px;
  margin-bottom: 56px;
}

article {
  max-width: 1280px;
  margin: 0 auto 80px;;
  padding: 0 20px;

  .content {
    background: white;
    padding: 40px 40px;
  }
}

.hidden {
  display: none;
}

.shown {
  display: block;
}

header {
  padding: 20px 40px;
  text-align: center;
  background: rgb(40, 40, 40);
  box-shadow: 0 5px 5px #ccc;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background: rgb(35, 255, 109);
  margin-bottom: 0;

  .content {
    background: transparent;
    padding: 20px 40px;
  }
}

h1 {
  font-size: 5.2rem;
  line-height: 6rem;
  margin-bottom: 6rem;
}

h2 {
  font-size: 3.2rem;
  line-height: 4rem;
  color: rgb(160, 160, 160);
  margin-bottom: 4rem;
}

a {
  font-size: inherit;
  line-height: inherit;
  text-decoration: underline;
  color: inherit;
}

p {
  font-size: 2rem;
  line-height: 2.8rem;
  margin-bottom: 2.8rem;
}

strong {
  font-weight: 700;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

ol li,
ul li {
  font-size: inherit;
  list-style-type: square;
  text-indent: -35px;
  padding-left: 40px;
  margin-bottom: 15px;
  list-style-position: inside;
  font-size: 2rem;
  line-height: 2.8rem;
}

button {
  background: rgb(35, 255, 109);
  -webkit-appearance: none;
  outline: none;
  border: 0;
  border-radius: 3px;
  padding: 12px 20px;
  margin-right: 20px;
  cursor: pointer;
  margin-bottom: 10px;

  &:focus,
  &:active {
    background: rgb(25, 245, 100);
  }
}

#meta > .content {
  padding-top: 20px;
  padding-bottom: 20px;
  background: rgb(35, 255, 109);
  text-align: right;
}

#meta-slot {
  position: relative;
}

.logo {
  width: 100px;
  height: 100px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-color: white;
  border-radius: 50px;
  box-shadow: 0px 0px 10px 0 rgb(60,60,60);
  // transform: translateY(-75px);
  position: absolute;
  top: -75px;
  left: 0;
}

.meta-contributors,
.meta-creator {
  // float: right;
  display: inline-block;
  font-size: 2.4rem;
  color: rgb(40, 40, 40);

  i {
    font-size: 3rem;
    margin-right: 10px;
  }
}

.meta-creator {
  padding-right: 30px;
}

#images-slot {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;

  figure {
    grid-column-end: span 4;

    img {
      max-width: 100%;
    }
  }
}

#buttons {
  position: absolute;
  top: -20px;
  left: 120px;
  font-size: 0;

  button {
    width: 60px;
    height: 60px;
    background: rgb(40, 40, 40);
    color: white;
    border-radius: 30px;
    padding: 0;
    font-size: 3rem;
    line-height: 110%;
    text-align: center;
    margin: 0;
    margin-bottom: 20px;
    box-shadow: 0 5px 5px #ccc;
    transform: translateY(-50%);
    margin-right: 20px;

    i {
      color: white;
    }
  }
}

#overview > .content h2 {
  margin-bottom: 80px;
}

#overview-slot {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 20px;

  .overview-item {
    grid-column-end: span 4;
    padding: 60px 20px 80px;
    background: #efefef;
    box-shadow: 0 5px 5px #ccc;
    transition: all .3s ease-out;
    cursor: pointer;
    position: relative;
    min-height: 12vh;
    margin-bottom: 40px;

    &:hover,
    &.active {
      background: rgb(35, 255, 109);
    }

    h2 {
      font-size: 2.4rem;
      line-height: 3.2rem;
      font-weight: 600;
      color: rgb(40, 40, 40);
      text-align: left;
      margin-bottom: 0;
    }

    .logo {
      width: 80px;
      height: 80px;
      position: absolute;
      top: 0;
      left: 20px;
      transform: translateY(-50%);
    }

    .overview-item__link {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .meta {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 20px;
      background: rgb(40, 40, 40);

      .meta-creator,
      .meta-contributors {
        font-size: 1.6rem;
        line-height: 2.4rem;
        color: white;
        display: inline-block;
        padding: 0;
        float: left;

        i {
          color: white;
          font-size: 22px;
          margin-right: 10px;
        }
      }

      .meta-creator {
        width: 75%;
      }

      .meta-contributors {
        width: 25%;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  main {
    padding-top: 100px;
    padding-bottom: 30px;
  }

  header {
    padding: 10px 20px;
  }

  header img {
    max-width: 150px;
  }

  h1 {
    font-size: 3.2rem;
    line-height: 4rem;
    margin-bottom: 2rem;
  }

  h2 {
    font-size: 2.2rem;
    line-height: 3rem;
    margin-bottom: 3rem;
  }

  p {
    font-size: 1.6rem;
    line-height: 2.4rem;
    margin-bottom: 2.4rem;
  }

  ol li,
  ul li {
    font-size: inherit;
    list-style-type: square;
    text-indent: -20px;
    padding-left: 35px;
    margin-bottom: 15px;
    list-style-position: inside;
    font-size: 1.6rem;
    line-height: 2.4rem;
  }

  article .content,
  footer .content {
    // // background: white;
    padding: 20px 20px;
  }

  .meta-contributors,
  .meta-creator {
    display: block;
    font-size: 1.4rem;
  }

  .meta-creator {
    padding-right: 0;
    margin-bottom: 10px;
  }

  .logo {
    width: 60px;
    height: 60px;
    border-radius: 30px;
    top: -50px;
    left: 0;
  }

  #images-slot {
    figure {
      grid-column-end: span 12;
    }
  }

  #overview-slot {
    .overview-item {
      padding-top: 40px;
      grid-column-end: span 12;

      .meta-contributors,
      .meta-creator {
        margin: 0;
      }

      .logo {
        width: 60px;
        height: 60px;
      }
    }
  }

  #buttons {
    left: 80px;

    button {
      width: 40px;
      height: 40px;
      background: rgb(40, 40, 40);
      color: white;
      border-radius: 30px;
      padding: 0;
      font-size: 2rem;
      line-height: 110%;
      text-align: center;
      margin: 0;
      margin-bottom: 20px;
      box-shadow: 0 5px 5px #ccc;
      transform: translateY(-50%);
      margin-right: 20px;

      i {
        color: white;
      }
    }
  }
}
